body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Droid Serif', 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

* {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6{
  margin: 0;
  padding: 0;
  text-transform: capitalize;
  text-decoration: none;
}

p, li, ul {
  margin: 0;
  padding: 0;
  text-transform: none;
  list-style-type: none;
}
.flex {
  display: flex;
}

.column {
  flex-direction: column;
}
.header {
    background-color: #4e4747;
    color: white;
    position: fixed;
    align-items: center;
    justify-content: space-between;
    top: 0;
    height: 130px;
    width: 100%;
    text-transform: capitalize;
    z-index: 7;
}

.nav {
    display: flex;
}

.open-mobile {
    display: flex;
}

.close {
    display: none;
}
.a-link {
    margin: 10px;
    text-decoration: none;
    text-transform: capitalize;
    font-size: 22px;
}

.a-link>img {
    width: auto;
    height: 124px;
    border-radius: 50%;
}
.a-link:visited {
    color: white;
}
.a-link:hover {
    color: #d27019;
}

a:active {
    color: #d49863;
}

.nav-mobile {
    position: absolute;
    right: 0;
    top: 110px;
    border: 1px solid #4e4747;
    background-color: #4e4747;
}

.nav-mobile>li {
    margin-bottom: 20px;
}

.navbar-toggler {
    border: 1px solid #4e4747;
    background-color: #4e4747;
    margin: 10px;
    display: none;
}
.animated-icon {
    width: 30px;
    height: 20px;
    position: relative;
    margin: 0px;
    cursor: pointer;
}

.animated-icon span {
    display: block;
    position: absolute;
    height: 3px;
    width: 100%;
    border-radius: 9px;
    opacity: 1;
    left: 0;
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    -webkit-transition: .25s ease-in-out;
    transition: .25s ease-in-out;
    z-index: 20;
    background: white;
}

.animated-icon span:nth-child(1) {
    top: 0px;
}
  
.animated-icon span:nth-child(2) {
    top: 10px;
}

.animated-icon span:nth-child(3) {
    top: 20px;
}

.animated-icon.open span:nth-child(1) {
    top: 11px;
    -webkit-transform: rotate(135deg);
            transform: rotate(135deg);
}
  
.animated-icon.open span:nth-child(2) {
    opacity: 0;
}
  
.animated-icon.open span:nth-child(3) {
    top: 11px;
    -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
}


@media (max-width: 600px) {
    .nav {
        display: none;
    }
    .navbar-toggler {
        display: flex;
    }
    .a-link>img {
        height: 100px;
    }
    .header {
        height: 105px;
    }
    .nav-mobile {
        top: 100px;
    }
}
.home {
    padding-top: 130px;
}

.home-img {
    background-repeat: no-repeat;
    background-attachment: scroll;
    background-position: center center;
    background-size: cover;
    width: 100%;
    height: 500px;
    z-index: 1;
    opacity: 0.25;
}
.home-container {
    height: 500px;
    width: 100%;
    padding-top: 20px;
    color: #d27019;
    z-index: 3;
    position: absolute;
    top: 118px;
    justify-content: space-between;
    margin-bottom: 40px;
    
}                    

.home-container>h1 {
    font-style: italic;
    margin-bottom: 35px;
    padding-top: 10px;
    /* text-shadow: 3px 1px 0px white; */
    font-size: 3em;
    opacity: 1;
}

.home-title {
    font-size: 1.5em;
    padding-top: 15px;
    padding-left: 15px;
    margin: 20px auto;
    /* text-shadow: 3px 1px 0px #c7bebe; */
}

.home-details {
    justify-content: space-between;
    align-items: center;
    margin: auto 20px;
}

@media (max-width: 600px) {
    .home {
        padding-top: 105px;
    }
    .home-container {
        padding-top: 0px;
        top: 105px;
        height: 480px;
    }
    .home-img {
        height: 470px;
    }
}
.portfolio-list {
    flex-wrap: wrap;
    justify-content: center;
}

.portfolio>h1 {
    color: #d27019;
    /* text-shadow: 3px 1px 0px #c7bebe; */
    font-style: italic;
    margin: 40px auto;
    padding-top: 140px;
}
.project-preview {
    border: 1px solid #e2d7d7;
    background-color: #efefef;
    box-shadow: 1px 1px 20px 2px #4e4d4d;
    width: 350px;
    height: 440px;
    justify-content: space-between;
    align-items: center;
    margin: 15px 25px;
    position: relative;
}

.a-img {
    width: 250px;
    height: 190px;
    background-size: cover; 
    background-position: center; 
    background-repeat: no-repeat;
    top: 55px;
    left: 50px;
    margin-bottom: 20px;
}

.project-title {
    height: 50px;
}
.project-name {
    font-size: 20px;
    font-weight: bold;
    align-items: center;
    margin-right: 5px;
}

.project-title>p {
    align-self: center;
}

.links {
    bottom: 15px;
    left: 50px;
    margin: 10px auto;
}

.links>a {
    border: 1px solid #dcc3c3;
    background-color: black;
    color: white;
    width: 70px;
    padding: 4px;
    margin: 0 10px;
    text-decoration: none;
}

.links>a:hover {
    background-color: #4e4747d9;
}

.ul-labels {
    bottom: 50px;
    left: 0px;
    overflow-x: auto;
    font-size: 0.75em;
    padding: 0;
    flex-wrap: wrap;
    margin: auto;
}

.label {
    border: 1px solid #dcc3c3;
    list-style-type: none;
    padding: 4px;
    margin: 0 5px;
    border-radius: 4px;
    min-width: 50px;
}

.preview-bottom {
    bottom: 10px;
}

.desc {
    height: 70px;
    padding: 0 10px;
}

@media (max-width: 817px) {
    .project-preview {
        margin: 15px;
    }
}

@media (max-width: 400px) {
    .project-preview {
        width: 90%;
        height: 500px;
    }
    .desc {
        height: auto;
    }
    .project-name {
        font-size: 18px;
    }
    .label {
        margin: 10px;
    }
}
.about {
    width: 70%;
    margin: 100px auto;
    line-height: 1.75;
}

.about>h1 {
    color: #d27019;
    /* text-shadow: 3px 1px 0px #c7bebe; */
    font-style: italic;
    padding-top: 140px;
}
.about-text-container {
    text-align: start;
}
.about-text {
    margin-bottom: 25px;
}
.education-list {
    margin-left: 50px;
}

.education-list>li {
    list-style-type: disc;
}

.labels {
    margin-bottom: 25px;
    flex-wrap: wrap;
}
.labels>p {
    border: 1px solid #dcc3c3;
    padding: 4px;
    margin: 10px 5px;
    border-radius: 4px;
    min-width: -webkit-fit-content;
    min-width: -moz-fit-content;
    min-width: fit-content;
}
.education-list {
    margin-bottom: 25px;
}
.cv {
    text-decoration: none;
    color: inherit;
    font-size: 1.2em;
}
.bold {
    font-weight: bold;
}
@media (max-width: 600px) {
    .about {
        width: 85%;
    }
}
.contact-list {
    align-items: center;
}

.contact>h3 {
    text-transform: none;
    text-transform: initial;
}

.contact>h1 {
    color: #d27019;
    /* text-shadow: 3px 1px 0px #c7bebe; */
    font-style: italic;
    margin: 40px auto;
    padding-top: 140px;
}

.link-icon {
    font-size: 28px;
    color: #4e4747d9;
    margin: 10px;
}

.link:hover, .phone:hover {
    color: black;
}

.link {
    align-self: center;
    text-decoration: none;
    color: #4e4747;
}
.phone-icon {
    cursor: pointer;
    color: #4e4747d9;
    font-size: 24px;
    margin: 10px;
}
.phone {
    align-self: center;
    color: #4e4747;
}
.phone-container {
    margin-bottom: 50px;
}

@media (max-width: 600px) {
    .link>p, .phone {
        font-size: 15px;
    }
    .contact-list {
        padding: 10px;
    }
    .contact>h3 {
        padding: 10px;
    }
}
.footer {
    height: 50px;
    width: 100%;
    background-color: #4e4747;
    opacity: 0.8;
    color: white;
    justify-content: center;
    align-items: center;
}
